import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Grid, Row, Col, Image, Button } from 'react-bootstrap';
import './SplashScreen.css';

export default class SplashScreen extends Component {
  render() {
    return (
      <div>
        <Grid fluid={true}>
          <Row className="show-grid text-center hero">
            <Col md={8} mdOffset={2} className="heroHeader">
              <h1>Giving A Voice To The People</h1>
              <h4>2000 Movements Began</h4>
              <Link to="/"><Button bsStyle="warning">Create Campaign</Button></Link>
            </Col>
          </Row>
          <Row fluid={true} className="show-grid text-center row-two">
            <Col mdOffset={1} md={3} xs={12}>
                <Image src={require("../../images/voice-announcer2.jpg")} circle className="row-two-image"/>
              <h4>Vocalize</h4>
            </Col>
            <Col md={4} xs={12}>
              <Image src={require("../../images/team.png")} circle className="row-two-image"/>
              <h4>Organize</h4>
            </Col>
            <Col md={3} xs={12}>
              <Image src={require("../../images/worldwide.png")} circle className="row-two-image"/>
              <h4>Mobilize</h4>
            </Col>
          </Row>
          <Row className="show-grid text-center">
            <Col md={12}>
            <h2>Popular Campaigns</h2>  
            </Col>  
          </Row>  
          <Row className="show-grid text-center">
            <Col md={4} xs={12}>
              <h4>Campaign 1</h4>
              <p>Explanation 1</p>
            </Col>
            <Col md={4} xs={12}>
              <h4>Campaign 2</h4>
              <p>Explanation 2</p>
            </Col>
            <Col md={4} xs={12}>
              <h4>Campaign 3</h4>
              <p>Explanation 3</p>
            </Col>
          </Row> 
        </Grid>
      </div> 
    )
  }
}



